import { Notify } from 'notiflix';
import axios from 'axios';

import { currentPage, pageRefs, onPageSearch } from '../pagination.js';
import { BASE_URL, API_KEY } from '../api/api.js';
import { showLoader, hideLoader } from '../loader.js';
import movieCard from '../../template/movieCard.hbs';
import { getGenres, dataCombine } from './fetchDateAndGenres.js';

Notify.init({
  width: '550px',
  position: 'center-top',
  distance: '150px',
  opacity: 1,
  fontSize: '16px',
  useIcon: false,
  fontAwesomeIconSize: '0px',
  failure: {
    background: 'transparent',
    textColor: '#FF001B',
  },
});

if (window.innerWidth < 768) {
  Notify.init({
    width: '300px',
    position: 'center-top',
    distance: '0px',
    fontSize: '12px',
    useIcon: false,
    fontAwesomeIconSize: '0px',
    failure: {
      background: 'transparent',
      textColor: '#FF001B',
    },
  });
}

const onChangeSize = () => {
  if (window.innerWidth < 768) {
    Notify.init({
      width: '300px',
      position: 'center-top',
      distance: '0px',
      fontSize: '12px',
      useIcon: false,
      fontAwesomeIconSize: '0px',
      failure: {
        background: 'transparent',
        textColor: '#FF001B',
      },
    });
  } else {
    Notify.init({
      width: '550px',
      position: 'center-top',
      distance: '150px',
      opacity: 1,
      fontSize: '16px',
      useIcon: false,
      fontAwesomeIconSize: '0px',
      failure: {
        background: 'transparent',
        textColor: '#FF001B',
      },
    });
  }
};
window.addEventListener('resize', onChangeSize);

const formEl = document.querySelector('#search-form');
const gallery = document.querySelector('.gallery');


const renderMovie = data =>
  gallery?.insertAdjacentHTML('beforeend', movieCard(data));

export const filmsParams = {
  query: '',
};

const fetchfilmsByKey = async params =>
  await axios
    .get(`${BASE_URL}/search/movie?api_key=${API_KEY}`, { params })
    .catch(error => console.error(error));

export const requestForMovie = async () => {
  hideLoader();
  const { data } = await fetchfilmsByKey(filmsParams);
  const movies = data.results;
  const totalPages = data.total_pages;

  if (movies.length === 0) {
    showLoader();
    return Notify.failure(
      'Search result not successful. Enter the correct movie name and try again.'
    );
  }
  console.log(totalPages);

  const { genres } = await getGenres();
  const fullInfo = dataCombine(movies, genres);

  renderMovie(fullInfo);
  showLoader();
};

const onSearch = e => {
  e.preventDefault();

  gallery.innerHTML = '';
  filmsParams.query = e.currentTarget.elements[0].value;

  if (filmsParams.query.length <= 1) {
    return Notify.failure(
      'Search result not successful. Enter the correct movie name and try again.'
    );
  }
  requestForMovie();
};

formEl?.addEventListener('submit', onSearch);
